@import "colors";

.category {
  margin-bottom: 80px;

  &-img__wrapper {
    display: flex;
    gap: 16px;
    margin-top: 24px;
    justify-content: center;
  }

  &-title {
    font-family: "Mont", sans-serif;
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    color: $primary-color;
    padding-top: 24px;

    &__first {
      padding-top: 15px;
    }
  }

  &-text {
    font-family: "Mont", sans-serif;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 21px;
    color: $gray-secondary;
    padding-top: 4px;
  }
}

@media screen and (max-width: 450px) {
  .category-wrapper img {
    width: 250px;
  }
}

@media (max-width: 1130px) {
  .category-img__wrapper {
    flex-direction: column;
    text-align: center;
  }
}

